.home-container{
    height:300px;
    color:white;
    padding-left:150px; 
}

.home-content h2{
    padding:5px;
    padding-top:70px;
    
}

.home-content h1{
    color:#e0b50f;
    padding: 5px;
}

.home-buttons{
  margin-top:20px; 
}

.home-buttons .btn-hire{
    background-color: #3b8ff3;
    color:white;
    width:160px;
}

.home-buttons .btn-hire:hover{
    background-color: white;
    color:#3b8ff3;
    width:200px;
    transition: 1s;
}

.home-buttons .btn-cv{
    background-color: #f29f67;
    color:white;
    margin-left:15px;
    width:160px;
}

.home-buttons .btn-cv:hover{
    opacity:80%;
    transition: 1s;
}

.theme-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    background-color: #1e1e1e;
    margin-top: 20px;
    text-align: center;
    position: fixed;
    cursor: pointer;
    z-index: 1;
    right: 0;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

@media screen and (max-width:600px){
    .home-container{
        margin: 0;
        padding: 0;
        margin-top: 50px;
    }

    .home-container h2{
        font-size: 24px;
    }

    .home-container h1{
        font-size: 24px;
    }

    .home-buttons .btn-hire{
        width:110px;
        font-size: 14px;
    }

    .home-buttons .btn-cv{
        width: 110px;
        font-size: 14px;
    }
}